<template><div><h1 id="spring-core-核心概念" tabindex="-1"><a class="header-anchor" href="#spring-core-核心概念"><span>Spring Core 核心概念</span></a></h1>
<h2 id="_1-spring-framework-概述" tabindex="-1"><a class="header-anchor" href="#_1-spring-framework-概述"><span>1. Spring Framework 概述</span></a></h2>
<p>Spring Framework 是一个开源的 Java 平台，为创建高性能、易测试、可重用的代码提供了全面的编程和配置模型。</p>
<h3 id="_1-1-核心特性" tabindex="-1"><a class="header-anchor" href="#_1-1-核心特性"><span>1.1 核心特性</span></a></h3>
<ul>
<li><strong>IoC容器</strong>：管理对象的创建和依赖关系</li>
<li><strong>AOP支持</strong>：面向切面编程的实现</li>
<li><strong>数据访问</strong>：简化数据库操作</li>
<li><strong>事务管理</strong>：提供声明式事务支持</li>
<li><strong>MVC框架</strong>：用于构建Web应用</li>
<li><strong>认证和授权</strong>：通过Spring Security实现</li>
<li><strong>远程调用</strong>：支持多种远程调用方式</li>
</ul>
<h2 id="_2-ioc-控制反转" tabindex="-1"><a class="header-anchor" href="#_2-ioc-控制反转"><span>2. IoC (控制反转)</span></a></h2>
<h3 id="_2-1-什么是ioc" tabindex="-1"><a class="header-anchor" href="#_2-1-什么是ioc"><span>2.1 什么是IoC</span></a></h3>
<p>IoC（Inversion of Control）是一种设计原则，它将传统上由程序代码直接操控的对象的调用权交给容器，通过容器来实现对象组件的装配和管理。</p>
<h3 id="_2-2-ioc容器的实现" tabindex="-1"><a class="header-anchor" href="#_2-2-ioc容器的实现"><span>2.2 IoC容器的实现</span></a></h3>
<p>Spring提供了两种IoC容器：</p>
<ul>
<li>BeanFactory</li>
<li>ApplicationContext</li>
</ul>
