<h3 id="_3-1-aop概念" tabindex="-1"><a class="header-anchor" href="#_3-1-aop概念"><span>3.1 AOP概念</span></a></h3>
<p>AOP（Aspect-Oriented Programming）允许程序模块化横切关注点，如事务管理、日志记录、安全等。</p>
<h3 id="_3-2-核心概念" tabindex="-1"><a class="header-anchor" href="#_3-2-核心概念"><span>3.2 核心概念</span></a></h3>
<ul>
<li><strong>切面（Aspect）</strong>：横切关注点的模块化</li>
<li><strong>连接点（Join Point）</strong>：程序执行的某个特定位置</li>
<li><strong>通知（Advice）</strong>：在切面的某个特定连接点上执行的动作</li>
<li><strong>切入点（Pointcut）</strong>：匹配连接点的规则</li>
<li><strong>引入（Introduction）</strong>：向现有的类添加新方法或属性</li>
</ul>
